title\">Applications</span>\n                            <img src=\"../../../../images/total-app-icon.svg\" alt=\"\">\n                        </div>\n                        <div class=\"card-body\">\n                            <div class=\"info\">\n                                <span class=\"info-number\">{{ allAppCount }}</span>\n                                <span class=\"info-text\">Total</span>\n                            </div>\n                            <div class=\"info\">\n                                <span class=\"info-number\">{{ licensedAppCount }}</span>\n                                <span class=\"info-text\">Licensed</span>\n                            </div>\n                        </div>\n                    </div>\n                </div>\n                <div class=\"col\">\n                    <div class=\"card \">\n                        <div class=\"card-header\">\n                            <span class=\"card-title\">Users</span>\n                            <i class=\"mdi mdi-account-outline\"></i>\n                        </div>\n                        <div class=\"card-body\">\n                            <div class=\"info\">\n                                <span class=\"info-number\">{{ userCount }}</span>\n                                <span class=\"info-text\">Total</span>\n                            </div>\n                            <div class=\"info\">\n                                <span class=\"info-number\">{{ userCount }}</span>\n                                <span class=\"info-text\">Licensed</span>\n                            </div>\n                        </div>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n<style scoped>\n.card-header img {\n    width: 24px;\n    height: 36px;\n}\n\n.card-header i {\n    font-size: 24px;\n}\n\n.info {\n    display: grid;\n    justify-items: center;\n}\n\n.info-text {\n    color: var(--Text-color, #2